@import url(https://fonts.googleapis.com/css2?family=Poppins&family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
* {
    margin: 0;
    padding: 0;
    font-family: 'Raleway';
    color:#000;
    text-decoration: none;
}

.linkHover {
    cursor: pointer;
    font-size: 24px;
    background-image: linear-gradient(
      to right,
      #F58220,
      #F58220 50%,
      #000 50%
    );
    background-size: 200% 100%;
    background-position: -100%;
    display: inline-block;
    padding: 5px 0;
    position: relative;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    transition: all 0.3s ease-in-out;
  }
  
  .linkHover:before{
    content: '';
    background: #F58220;
    display: block;
    position: absolute;
    bottom: -3px;
    left: 0;
    width: 0;
    height: 3px;
    transition: all 0.3s ease-in-out;
  }
  
  .linkHover:hover {
   background-position: 0;
  }
  
  .linkHover:hover::before{
    width: 100%;
  }

  .footer {
    background-color: #373737;
    margin-top: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.footer .footerContainers {
    margin: 30px 10px 0px 10px;
    display: flex;
    flex-direction: column;
}

.footer .footerContainers .shopInfo {
    height: 70px;
    margin-top: 10px;
    margin-bottom: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.footer .footerContainers .shopInfo .phoneTag {
    color: #fff
}

@media (max-width:1200px) {
    .footer {
        flex-direction: column;
        align-items: flex-start;
    }
    .footer .footerContainers {
        margin: 20px;
    }
}

